body {
    background-color: #fafafa;
}

a{
    text-decoration: none !important;
    cursor: pointer;
}
.inline{
    display: inline-block !important;
}
.flex{
    display: -ms-flexbox;
    display: flex;
}
.flex-coulmn{
    -ms-flex-direction: column;
        flex-direction: column;
}
.flex-coulmn ul{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.flex-coulmn ul li{
    margin-left: 30px;
}

.wrapp {
    border-radius: 16px;
    padding: .75rem 1.25rem;
    margin-bottom: -1px;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.125);
}
.avatar{
    width:77px;
    margin-right: 22px;
    height: 100%;

}
.center{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1 1;
        flex: 1 1;
    height: 100vh;
}
.nav-ul{
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 12px;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}
.nav-ul a.active  {
    background: #007bff;
    color: white !important;
    padding: 10px;
    border-radius: 3px;
}
ul.options{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
ul.options .active{
    color: #007bff;
}
.box{
    border: 1px solid #f1f1f1;
    border-bottom: 1px solid #28a745;
    padding: 16px 18px;
    border-radius: 4px;
    position: relative;
}
.delete{
    position: absolute;
    top: 0;
    right: 0;
    padding: 0px 4px;
}


